        result: 0 
      }
    },
    methods: {
      /* 根据上述参数获取得病概率 */
      getResult() {
          this.result = (100*
            (1-
              1/(1+Math.exp(
                  -0.63941+1.55552*Number(this.value1)
                  -0.75635*Number(this.value2)
                  -0.44803*Number(this.value3)
                  +0.58645*Number(this.value4)
                  +0.76076*Number(this.value5)
                )
              )
            )
          ).toFixed(2)
      }
    },
    mounted(){
      this.getResult();
    }

  };

